// volumes

export const getVolumes = () => backend.wsRequest('/trades/volumes').then(mapVolumes);

export const removeTradeVolumesListener = () => {
  backend.socket.removeAllListeners('trades_volumes');
};

export const onTradeVolumesChange = (callback) => {
  removeTradeVolumesListener();
  backend.socket.on('trades_volumes', throttle(flow([mapVolumes, callback]), THROTTLE_DELAY_VOLUMES, { leading: false }));
};


// Gas price

/**
 * Fetches Gas Price from backend.
 *
 * @param {{ gasPriceType: string }} params
 * @returns {Promise<{fast: BigNumber|null}>}
 */
export const getGasPrice = async (params) => {
  const event = await backend.wsRequest('/eth/gas-price');
  return mapGasPrice(event, params);
};

export const removeGasPriceListener = () => {
  backend.socket.removeAllListeners('gas_price');
};

/**
 * React to gas price change
 *
 * @param handler {function}
 * @param getParams {function} returns always resolved Promise
 */
export const onGasPrice = ({ handler, getParams }) => {
  removeGasPriceListener();
  backend.socket.on('gas_price', async event => (
    handler(mapGasPrice(event, await getParams()))
  ));
};

// Funds

/**
 *
 * @param userAddress {string}
 * @param contractAddress {string}
 * @returns {Promise<{data: object}>}
 */
export const getPendingBalanceOperations = async ({ userAddress, contractAddress }) => {
  const { data } = await backend.wsRequest('/funds/get', {
    filters: {
      user_address: userAddress,
      contract_address: contractAddress,
      is_pending: true,
    },
    limit: 1000,
  });
  return {
    data: data.map(mapOperation),
  };
};

export const removeFundsListener = () => {
  backend.socket.removeAllListeners('funds');
};

export const onFunds = (callback) => {
  removeFundsListener();
  backend.socket.on('funds', e => callback(mapOperation(e)));
};

