/**
 * Convert backend error to string
 * @param {Error} err
 * @returns {String}
 */
export const backendErrorToString = (err) => {
  const errors = get(err, 'response.data.errors', []);

  if (errors.length > 0) {
    return errors.map(e => e.message).join(', ');
  }

  return err.message;
};

