/**
 * @module store/order-book/getters
 *
 * @version 1.0.0
 */
import { transformOrderBookElements } from './helpers';

/**
 * Getter:       
 *
 * @since Version 1.0.4
 */
const orderBookLoading = state => state.orderBookLoading;

/**
 * Getter:    
 *
 * @since Version 1.0.4
 */
const getAsks = state => transformOrderBookElements(state.asks, false);


/**
 * Getter:    
 *
 * @since Version 1.0.4
 */
const getBids = state => transformOrderBookElements(state.bids, true);


export default {
  getAsks,
  getBids,
  orderBookLoading,
};



