import BigNumber from 'bignumber.js';
import { SELL_TOKEN_ORDER, BUY_TOKEN_ORDER } from '@/util/constants';
import backend from '../endpoint';

export const createOrder = async ({ contractAddress, futuresContractHash, userAddress, amount, price, side, signature, stopPrice }) => {
  const { nonce, v, r, s } = signature;
  return backend.http.post('/api/futures/orders', {
    contract_address: contractAddress,
    futures_contract_hash: futuresContractHash,
    user_address: userAddress,
    amount,
    price,
    side,
    nonce,
    v,
    r,
    s,
    order_hash: signature.orderHash,
    stop_price: stopPrice,
  });
};

export const cancelOrder = async ({ contractAddress, userAddress, orderHash, signature }) => {
  const { nonce, v, r, s } = signature;
  return backend.http.delete('/api/futures/orders', {
    data: {
      contract_address: contractAddress,
      user_address: userAddress,
      order_hash: orderHash,
      nonce,
      v,
      r,
      s,
      cancel_hash: signature.cancelHash,
    },
  });
};

export const orderTransformer = (o) => {
  const m = {
    id: o.id,
    contractAddress: o.contract_address,
    futuresContractHash: o.futures_contract_hash,
    userAddress: o.user_address,
    side: o.side,
    price: BigNumber(o.price),
    stopPrice: o.stop_price ? BigNumber(o.stop_price) : null,
    amount: BigNumber(o.amount),
    amountFilled: {
      pending: BigNumber(o.amount_filled.pending),
      approved: BigNumber(o.amount_filled.approved),
      total: BigNumber(o.amount_filled.total),
    },
    orderHash: o.order_hash,
    nonce: o.nonce,
    r: o.r,
    s: o.s,
    canceledAt: o.canceled_at !== null ? new Date(o.canceled_at) : null,
    createdAt: new Date(o.created_at),
    isStopped: o.is_stopped,
    isRejected: o.is_rejected,
    isActive: o.is_active,
    isCanceled: o.canceled_at !== null,
    type: o.side ? BUY_TOKEN_ORDER : SELL_TOKEN_ORDER,
  };

  m.isFulfilled = m.amount.isGreaterThanOrEqualTo(m.amountFilled);

  return m;
};

export const getOrders = async ({ userAddress, isActive, limit = 50, offset = 0 }) => {
  const params = {
    user_address: userAddress,
    is_active: isActive,
    limit,
    offset,
  };

  const resp = await backend.http.get('/api/futures/orders', { params });

  return resp.data;
};

export const removeMyOrderListener = () => {
  backend.socket.removeAllListeners('futures_my_orders');
};

export const onMyOrderChange = (callback) => {
  removeMyOrderListener();
  backend.socket.on('futures_my_orders', callback);
};



