/**
 * :   
 *
 * @param {(BigNumber|null)} a -    
 * @param {(BigNumber|null)} b -    
 * @param {(String|null)} property - ,  a  b 
 *
 * @version 1.0.0
 */
export default (property, order = true) => (b, a) => {
  let x;
  let y;

  if (order) {
    x = property ? a[property] : a;
    y = property ? b[property] : b;
  } else {
    y = property ? a[property] : a;
    x = property ? b[property] : b;
  }


  if (x === null && y === null) {
    return 0;
  }

  if (x === null) {
    return -1;
  }

  if (y === null) {
    return 1;
  }

  if (x.eq(y)) {
    return 0;
  }

  if (x.gt(y)) {
    return 1;
  }

  return -1;
};



