import BigNumber from 'bignumber.js';
import moment from 'moment';

import backend from '../endpoint';

export const positionTransformer = p => ({
  side: p.side,
  contractAddress: p.contract_address,
  futuresContractHash: p.futures_contract_hash,
  futuresAssetHash: p.futures_asset_hash,
  userAddress: p.user_address,
  positionHash: p.position_hash,
  size: BigNumber(p.size),
  price: BigNumber(p.price),
  expiresInSeconds: p.expires_in_seconds ? moment().add(p.expires_in_seconds, 'seconds').format('MMM DD @ HH:mm') : 0,
  unrealizedPL: BigNumber(p.unrealized_pl),
  collateral: BigNumber(p.collateral),
  markPrice: BigNumber(p.mark_price),
  isClosable: p.is_closable,
});

export const getPositions = async ({ userAddress }) => {
  const params = {
    user_address: userAddress,
  };

  const resp = await backend.http.get('/api/futures/positions', { params });

  return resp.data;
};



